  .back {
    position: absolute;
    top: 10px;
    left: 10px;
    margin-bottom: 10px;
    background: #444;
    color: #fff;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 1.5rem;
    letter-spacing: 0.1rem;
    padding: 0.75rem 1.5rem;
    transition: 0.5s;
}

.back:hover {
    letter-spacing: 0.25rem;
    background: #1e9bff;
    box-shadow: 0 0 35px #1e9bff;
}

  .item {
    background-color: rgba(0, 0, 0, 0.5);
    font-size: 24px;
    border-radius: 20px;
    padding: 10px;
    width: 600px;
    text-align: center;
    text-decoration: none;
    color: white;
    margin: auto;
    margin-top: 60px;
}

